import Vue from 'vue'
import Router from 'vue-router'
import DataManagement from '@/pages/DataManage'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      component: DataManagement
    },
    {
      path: '/data',
      name: 'DataManagement',
      component: DataManagement
    }
  ]
})



